#root {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mapDiv {
  padding: 0;
  margin: 0;
  height: 50%;
  width: 50%;
}
.t-container {
  margin: 0 auto;
}
.t-tabs{
  list-style-type: none;
  margin: 0;
  box-sizing: border-box;
}
.t-tabs li {
  color: #dedede;
  padding: 0.5em 0.7em;
  text-decoration: none;
  float: left;
  text-align: center;
  font-size: 1.1em;
  background: #717171;
  border-bottom: 0;
  min-width: 100px;
  margin-right: 2px;
  margin-bottom: 0 !important;
  box-sizing: border-box;
}
.t-tabs li:hover {
  background-color: #545454;
  color: #d2d2d2;
}
.t-tabs li.selected {
  color: #458ace;
  background: #333;
}

.menu-btn {
    background-color: white;
    color: #93b2c8;
    padding: 16px;
    font-size: 16px;
    border: none;
}

.menu-item {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.menu {
    text-align: center;
    width:100vw;
    border-bottom: 1px solid #93b2c8;

}

.dropdown-content a:hover {background-color: #ddd;}

.menu-item:hover .dropdown-content {display: block;}

.menu-item:hover .menu-btn {

    box-shadow: 0 2px 0 #0090FF;
    border-bottom-size: 10px;
    border-bottom-color: #0090FF;
    color:#0090FF;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
